.pageload_container{width:100%;position:absolute;z-index:999;height:100vh;display:grid;place-items:center;background-color:#010349}.pageload_container .loader{border:5px solid #f3f3f3;border-radius:50%;border-top:5px solid #010349;width:60px;height:60px;animation:spin 1s linear infinite}@media(max-width: 500px){.pageload_container .loader{border:3px solid #f3f3f3;border-top:3px solid #010349;width:40px;height:40px}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
.nav_container{background-color:#010349;height:60px;padding:0 20px;position:fixed;top:0;z-index:999;width:100%}.nav_container .nav_max{max-width:1200px;width:100%;display:flex;justify-content:space-between;align-items:center;height:inherit;margin:0 auto}.nav_container .nav_max .brand_logo{font-family:"Caveat",cursive;color:#fff;padding-right:20px}.nav_container .nav_max nav{overflow-x:auto;overflow-y:hidden;-ms-overflow-style:none;scrollbar-width:none}@media(max-width: 450px){.nav_container .nav_max nav{-webkit-mask-image:linear-gradient(to right, transparent, black 60px, black 90%, transparent)}}.nav_container .nav_max nav .nav_link{color:#fff;text-decoration:none;margin-left:1rem;transition:all .2s}.nav_container .nav_max nav .nav_link:hover{color:#c9c9c9}.nav_container .nav_max nav::-webkit-scrollbar{display:none}
.notFound-wrapper{margin-top:calc(60px + 100px);text-align:center;padding-bottom:2rem}.notFound-wrapper .big{font-weight:500;font-size:5rem}.notFound-wrapper img{width:80%;margin-top:3rem;max-width:500px}
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{margin:0;padding:0;box-sizing:border-box}main{background-color:#f5f5f5}html{scroll-behavior:smooth}.modal_container{position:fixed;z-index:9999;width:100%;display:grid;top:0;min-height:100vh;background:rgba(0,0,0,.6705882353);place-items:center;padding-top:2.5rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.modal_container .modal_close{color:#fff;position:absolute;top:20px;font-size:2.5rem;right:2rem;cursor:pointer;z-index:999}.modal_container img{width:60%;max-height:85vh}@media(max-width: 750px){.modal_container img{width:100%}}a{text-decoration:none}
